/*
* author: ZJJ
* description: switch 
* :
* @checked: ko Boolean 
* @disabled: ko Boolean 
* @size: String:large, small (, )
* */
import template from './index.html'
import ko from 'knockout'
function init (params) {
  var self = this
  const PREFIX = 'y-switch-' // 
  this.disabled = params.disabled ? params.disabled : ko.observable(false)
  this.checked = params.checked || ko.observable(false) // ko Boolean 
  this.size = params.size ? (PREFIX + params.size) : '' //   String 
  this.sizeClass = ko.computed(() => {
    return self.disabled() ? self.checked() ? `${this.size} ${PREFIX}checked ${PREFIX}disabled` : `${this.size} ${PREFIX}disabled` : self.checked() ? `${this.size} ${PREFIX}checked` : `${this.size} ${PREFIX}`
  }, this)
  this.checkSelect = function () {
    if (self.disabled()) {
      return false
    } else {
      this.checked() ? this.checked(false) : this.checked(true)
    }
  }
}
export default {
  name: 'switch',
  init,
  template
}



